.movie-card {
	box-sizing: border-box;
  border: 3px solid black;
  padding: 10px;
	width: 250px;
	color: white;
	transition: 0.3s;
}

.movie-card:hover {
	transform: scale(1.2);
	border-color: #818181;
	transition: 0.3s;
	cursor: pointer;
}

img{
	width: 100%;
	max-width: max-content;
}

a {
	color: aliceblue;
	text-decoration: none;
}